export default "import { useCallback, useEffect, useState } from 'react';\n\nexport const useAutoScroll = (emblaApi) => {\n    const [autoScrollIsPlaying, setAutoScrollIsPlaying] = useState(false);\n    \n    const onAutoScrollButtonClick = useCallback((callback) => {\n        const autoScroll = emblaApi?.plugins()?.autoScroll;\n        if (!autoScroll)\n            return;\n        \n        autoScroll.stop();\n        callback();\n    }, [emblaApi]);\n    \n    const toggleAutoScroll = useCallback(() => {\n        const autoScroll = emblaApi?.plugins()?.autoScroll;\n        if (!autoScroll)\n            return;\n        \n        const playOrStop = autoScroll.isPlaying()\n            ? autoScroll.stop\n            : autoScroll.play;\n        playOrStop();\n    }, [emblaApi]);\n    \n    useEffect(() => {\n        const autoScroll = emblaApi?.plugins()?.autoScroll;\n        if (!autoScroll)\n            return;\n        \n        setAutoScrollIsPlaying(autoScroll.isPlaying());\n        emblaApi\n            .on('autoscroll:play', () => setAutoScrollIsPlaying(true))\n            .on('autoscroll:stop', () => setAutoScrollIsPlaying(false))\n            .on('reinit', () => setAutoScrollIsPlaying(autoScroll.isPlaying()));\n    }, [emblaApi]);\n    \n    return {\n        autoScrollIsPlaying,\n        toggleAutoScroll,\n        onAutoScrollButtonClick\n    };\n};\n";